import returns from '../../common/returns.vue'
export default {
	components:{
		returns
	},
	data() {
		return {
			title: '拼团操作',
			data:'',
			id:'',
			type:''
			
		}
	},
	methods:{
		open(){
			uni.navigateTo({
				url:'./group_order?type='+this.type+'&id='+this.id
			})
		},
		jump(){
			uni.navigateTo({
				url:'../assemble'
			})
		}
	},
	onLoad(options) {
		this.id = options.id
		this.type = options.type
		this.service.entire(this,'get',this.APIconfig.api_root.subhome.threehome.g_Detail,{id:options.id},function(self,res){
			console.log(res)
			self.data = res.data.goods
		})
	}
}
